#root
{
    position: relative;
}

.content
{
    max-width: 1280px;
    padding: 40px 30px;
    margin: auto;
}

.title
{
    font-size: 24px;
}

.catalog
{
    padding: 20px 0;
}

.catalog__header
{
    padding: 0 0 20px;
    margin: 0 0 20px;

    border-bottom: 1px solid #dddddd;
}

.catalog__info
{
    font-size: 16px;

    text-transform: uppercase;
}


